@import "../../../style/theme/default/var.css";
@component-namespace mailtemplate{
  @b header{
    @utils-clearfix;
    margin-bottom: var(--theme-margin-small);
    border-bottom: 1px solid var(--theme-border-color-primary);
    & .title{
      font-size: 14px;
      line-height: 30px;
    }
    & label{
    /*font-size: 12px;*/
      }
  }
  @b form {
    @e tags{
      cursor: pointer;
      margin-right: 4px;
      &:active{
      background-color: var(--color-white);
       }
    }
    @e input {
      .el-input__inner{
        border-color:transparent;
        &:focus{
      border-bottom:1px solid var(--theme-color-primary);
         }
      }
    }
    .is-focus:focus {
      outline: none;
      border-color: var(--theme-color-primary);
    }
    .el-form-item.el-block-box{
      padding: 0 var(--theme-padding-base);
    }
  }
@b editor{
  margin-bottom: var(--theme-margin-small);
  & table{

    border-collapse: collapse;
    border-spacing: 0;
  }
  @e table{
    width: 100%;
    td {
      border:1px solid var(--theme-border-color-primary);
      padding: 5px 10px;
      font-size: var(--theme-font-size-base);
    }
    td.title{
      border:1px solid var(--theme-border-color-primary);
      border-top:0;
    }
    &:first-child{
      td.title{
        border-top:1px solid var(--theme-border-color-primary);
      }
    }
  }
  .inner{
    margin: 0 auto;
    width: 90%;
    tr {
      td:last-child{
        border-right:1px solid var(--theme-border-color-primary);
      }
    }
  }
}
/*推荐商品*/
  @b select {
    margin-bottom: var(--theme-margin-small);
    @e table{
      border-collapse: collapse;
      border-spacing: 0;
      td {
        border:1px solid var(--theme-border-color-primary);
        border-right:0;
        font-size: var(--theme-font-size-small);
      }
      tr {
        td:last-child{
          border-right:1px solid var(--theme-border-color-primary);
        }
      }
      .goods-item{
        padding:var(--theme-padding-small);
        width:25%;
        vertical-align: top;
      }

      .goods-addpic{
        text-align: center;
        width:178px;
        height: 178px;
        line-height:178px;
        margin:0 auto 5px;
      /*  max-width: 100%;*/
        border:1px solid var(--theme-border-color-primary);
        position:relative;
        a {
          display: block;
          width: 100%;
          height:100%;
          font-size: 16px;
        }
        img{
          width: 100%;
          height:100%;
          vertical-align: top;
        }

        &.has-selected {
          .goods-deletepic {
            display: inline-block;
          }
        }
        .goods-deletepic {
          display: none;
          position: absolute;
          right:0;
          top:0;
          z-index: 22;
          line-height: 1;
          color: var(--color-white);
          font-size: 18px;
          background: rgba(0,0,0,.3);
          padding: 2px;
          cursor: pointer;
          &:hover{
             color: var(--theme-color-secondary);
           }
        }
      }
     .goods-name{
       line-height: 18px;
       margin-bottom: 5px;
       @utils-ellipsis 2;
       width:100%;
       height:36px;
     }
     .goods-addprice{
       border-collapse: collapse;
       border-spacing: 0;
       td{
         padding: 5px;
       }
     }

    }
  }
/* /end推荐商品*/
/* / 邮件详情 预览*/
@b preview {
  background: #fff;
  line-height: 1.42857143;
  & .text-title{
    font-size: var(--theme-font-size-base);
      margin-bottom: 5px;
  }
  & .preview-header{
    margin-bottom: 5px;
  }
  & .preview-editor{
  text-indent: 2em;
  }
  & .goods-addpic{
      width: 172px;
      height: 172px;
      line-height: 172px;
  }
& .preview-message{
 p {
   margin: 0;
 }
  }
}
/* /end 邮件详情预览*/
}



